
























































































































    import { Component, Vue, Watch, Prop } from "vue-property-decorator";

    @Component
    export default class Player extends Vue {
        player: HTMLVideoElement;
        isPlaying: boolean = false;
        isMuted: boolean = false;
        currentTime = '';
		duration = '';
		showTime = false;
        remainingTime = '';
        progressBar; //: HTMLElement;

        mounted() {
            this.player = document.getElementById('h-player') as HTMLVideoElement;
            setTimeout(() =>{
                this.progressBar = document.getElementById('player__progress-span');
                var base = 0;
				this.player.onloadedmetadata = () => {
                    base = 100/this.player.duration;
                    this.remainingTime = this.formatTime(this.player.duration);
                }
				this.player.onplay  = (e) => { this.isPlaying = true; this.showTime = true; };
				this.player.onpause = (e) => this.isPlaying = false;
                this.player.onended = (e) => this.isPlaying = false;
  
				this.player.ontimeupdate = (e) => {
					this.currentTime = this.formatTime(this.player.currentTime);
					this.duration = this.formatTime(this.player.duration);
					this.remainingTime = this.formatTime(this.player.duration - this.player.currentTime);
					var percentage = base * this.player.currentTime;
					this.progressBar.style.width = percentage + '%';
					this.progressBar.innerHTML = percentage + '% played';
				}
			},2);
        }

        play() {
			this.player.play();
			this.isPlaying = true;
		}

		pause() {
			this.player.pause(); 
            this.isPlaying = false;
		}

		mute() {
			this.player.muted = true;
			this.isMuted = true;
		}

		unmute() {
			this.player.muted = false; 
			this.isMuted = false;
        }
        
        formatTime(seconds, guide = seconds) {
			 seconds = seconds < 0 ? 0 : seconds;
			let s = Math.floor(seconds % 60);
			let m = Math.floor(seconds / 60 % 60);
			let h = Math.floor(seconds / 3600);
			const gm = Math.floor(guide / 60 % 60);
			const gh = Math.floor(guide / 3600);

			if (isNaN(seconds) || seconds === Infinity) {
				h = m = s = 0 ;
			}

			var hours = (h > 0 || gh > 0) ? h + ':' : '';
			var minute  = (((h || gm >= 10) && m < 10) ? '0' + m : m) + ':';
			var second = (s < 10) ? '0' + s : s;

			return hours + minute + second;
		}
        
    };
